@tailwind base;
@tailwind components;
@tailwind utilities;

body{
  @apply bg-neutral-200;
}

@layer components{
  .nav-wrapper{
    @apply bg-[rgba(121,119,119,0.32)] backdrop-blur-md shadow-sm flex w-full items-center justify-center sticky top-0 z-10 max-lg:px-2 ;
  }

  .navbar{
    @apply w-full h-[50px] flex justify-between items-center max-w-[1280px] bg-transparent mx-auto;
  }

  .nav-link.active{
    @apply text-green-600 drop-shadow-md;
  }
  
  .nav-link{
    @apply rounded-lg py-2 px-4 font-sans font-semibold text-base text-slate-600 hover:bg-[rgba(83,87,88,0.5)] hover:text-white;
  }

  .logo{
    @apply font-black font-sans text-2xl;
  }

  .logo span{
    @apply text-red-600;
  }

  .image-slider{
    @apply aspect-[16/5.0625] max-md:aspect-[16/9];
  }

  .slider-wrapper{
    @apply my-4;
  }

  .slide{
    @apply relative;
  }

  .darkshadow{
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    height: 70%;
    background-image: linear-gradient(0deg, rgba(0,0,0,0.7483368347338936) 0%, rgba(14,14,14,0) 99%);;
  }

  .carousel{
    @apply flex gap-4 overflow-x-scroll ;
  }

  .carousel,#menu{
    -ms-overflow-style: none;  /* Internet Explorer 10+ */
    scrollbar-width: none;  /* Firefox */
  }
      
  .carousel::-webkit-scrollbar,#menu::-webkit-scrollbar { 
      display: none;  /* Safari and Chrome */
  }

  .slider{
    user-select: none;
  }

  .tag{
    @apply rounded-md text-white odd:bg-lime-500 even:bg-red-600 px-1;
  }

  .menu-link{
    @apply text-white text-lg;
  }

  .search-wrapper{
    @apply bg-gray-600 flex p-4 flex-col items-start justify-center text-white;
  }

  .filter-wrapper{
    @apply mt-4 w-full;
  }

  .filter{
    @apply flex w-full gap-2 flex-wrap mt-2 text-base max-md:text-sm text-black; 
  }

  .btn{
    @apply text-lg rounded-lg  font-bold px-2 py-1 border-[3px] bg-lime-600 border-lime-600 cursor-pointer w-min;
  }

  .results{
    @apply p-4 max-lg:px-0;
  }

  .resultLayout{
    @apply grid grid-cols-[repeat(6,1fr)] max-lg:grid-cols-[repeat(4,1fr)] max-sm:grid-cols-[repeat(2,1fr)];
  }
}